.container {
  position: relative;
  text-align: center;
}
.text {
  color: rgb(185, 0, 0);
  text-transform: uppercase;
  font-size: 25px;
  text-align: center;
  font-weight: bold;
  letter-spacing: 14px;
  position: relative;
}

.text::before,
.text::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.text::before {
  color: #ff00c1;
  animation: glitch-effect 3s infinite;
}

.text::after {
  color: #3498db;
  animation: glitch-effect 2s infinite;
}

@keyframes glitch-effect {
  0% {
    left: -2px;

    top: -2px;
  }

  25% {
    left: 2px;

    top: 0px;
  }

  50% {
    left: -1px;

    top: 2px;
  }

  75% {
    left: 1px;

    top: -1px;
  }

  100% {
    left: 0px;

    top: -2px;
  }
}
.message {
  padding: 40px;
  text-align: center;
  width: 100%;
  display: block;
  font-size: 18px;
  color: #b31919;
}
.button {
  padding: 10px 15px;
  border: none;
  border-radius: 3px;
  width: 180px;
  margin: 0 10px;
  color: azure;
  text-transform: capitalize;
  background-color: crimson;
  cursor: pointer;
  font-size: 17px;
}
.button:hover {
  background-color: rgb(197, 32, 65);
}
