.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 15px;
  height: 15px;
  animation: loadings 4s infinite linear;
}

.loading::before,
.loading::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 15px;
  height: 15px;
  border-radius: 15px;
  animation: loading 1.5s infinite linear;
}

.loading::before {
  box-shadow: 15px 15px #e77f67, -15px -15px #778beb;
}

.loading::after {
  box-shadow: 15px 15px #f8a5c2, -15px -15px #f5cd79;
  transform: translate(-50%, -50%) rotate(90deg);
}

@keyframes loading {
  50% {
    height: 45px;
  }
}
@keyframes loadings {
  0% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
}
